import React, { Component } from "react";
import HomeLayout from "../components/HomeLayout";
import CartelHome from "../components/CartelHome";
import MarcoLogo from "../components/MarcoLogo";

const info =
  "asasasss asass asass asasasss asass asassasasasss asass asassasasasss asass asasasss asass asassasasasss asass asassasasasss asass asassasasasss asass asassasassasasasss asass asassasasasss asass asassasasasss asass asassasasasss asass asass";

const links = [
  {
    titulo: "Servicios",
    id: "#servicios"
  },
  {
    titulo: "Contacto",
    id: "#contacto"
  },
  {
    titulo: "Turnos",
    id: "#turnos"
  }
];

class Home extends Component {
  render() {
    return (
      <HomeLayout>
        <CartelHome info={info} />
        <MarcoLogo links={links} />
      </HomeLayout>
    );
  }
}
export default Home;



